.search-form {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 300px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.photo-card {
  background-color: #f2f2f2;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  margin-top: 10px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

